.table {
  max-width: 1000px;
  display: flex;
  flex-direction: row;
}
.playerscolumn {
  list-style-type: none;
  min-width: 10%;
  max-width: 15%;
  display: flex;
  flex-direction: column;
  padding-inline-start: 0px;
}
.dealscontainer {
  width: 60%;
  display: flex;
  flex-direction: row;
}
.dealcolumn {
  width: 14.28%;
  display: flex;
  list-style-type: none;
  flex-direction: column;
}
.dealcolumn .dealheader {
  display: flex;
}
.dealcolumn .inputcontainer {
  border-style: solid;
  border-width: 1px;
  border-collapse: collapse;
  display: flex;
  width: 100%;
}
.dealcolumn .inputcontainer input {
  height: 18px;
  max-width: 90%;
  text-align: right;
  border-style: none;
  border-width: 1px;
  border-color: black;
  border-image: none;
  border-collapse: collapse;
  outline-style: none;
  padding: 0px;
  display: flex;
  border-radius: 0;
  webkit-border-radius: 0;
  -webkit-appearance: none;
  appearance: none;
}
.truncated {
  display: inline;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.nameheader {
  display: inline;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.playername {
  display: inline;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 20px;
}
.dealheader {
  display: inline;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.resultcolumn {
  width: 7.5%;
  display: flex;
  flex-direction: column;
}
.resultcolumn .dealheader {
  display: flex;
}
